import React from 'react'
import { connect } from 'react-redux'
import { Input, Button, Icon, Modal } from 'antd'
import { getDictDataList, deleteDictData } from '/api/dict'
import Common from '/page/common.jsx'
const ButtonGroup = Button.Group
import DictDataForm from '/components/dict/dictDataForm.jsx'
import { handleTreeData, handleLocation } from '/api/tools'


class DictData extends Common{
	async componentWillMount () {
		let dictid = handleLocation().dictid
		let istree = handleLocation().istree
		this.setState({dictid, istree})
		this.search()
	}

	state = {
		dictid: undefined,
		istree: 1,
		columns: [{
			title: '名称',
			dataIndex: 'name'
		},{
			title: '编码',
			dataIndex: 'code'
		}, {
			title: 'ID',
			dataIndex: 'id'
		}, {
			title: '排序',
			dataIndex: 'num'
		}, {
			title: '备注',
			dataIndex: 'tips'
		}, {
			title: '备用字段1',
			dataIndex: 'field1'
		}, {
			title: '备用字段2',
			dataIndex: 'field2'
		}, {
			title: '备用字段3',
			dataIndex: 'field3'
		}],
		selected: {},
		loading: true,
		tabledata: [],
		modalconf: {visible: false, item: {}},
		search: {name: ''},
		pagination: false,
		treeMenu: []
	}

	search = async _ => {
		await this.setState({loading: true, selected: {}})
		let search = Object.assign({dictid: this.state.dictid}, this.state.search)
		if (!search.condition) delete search.condition	
		return getDictDataList(search)
		.then(res => {
			console.log(res.data)
			let data = (f => f(f))(f => list => list.map(val => {
				let baseItem = Object.assign({}, val, { key: val.id })
				if (val.childList && val.childList.length) {
					let item = Object.assign({}, baseItem, {
						children: f(f)(val.childList)
					})
					delete item.childList
					return item
				}
				return baseItem
			}))(res.data)

			this.setState({
				tabledata: data, 
				loading: false,
				treeMenu: handleTreeData(data, 'name', 'code', 'children')
			})
		})
	}

	deleteback = _ => {
		this.search()
	}
	delete = _ => {
		this.handleOk(deleteDictData, 'id', '删除', 'deleteback')
	}

	done = async _ => {
		let type = this.state.modalconf.type
		let config = {}
		config.modalconf = {visible: false, item: {}}
		this.setState(config)
		if (type == 'add') {
			this.research()
		} else {
			await this.setState({selected: {}})
			this.search()
		}
	}

	// renderSearch = _ => <div>
	// 	<div className="mgrSearchBar">
	// 		<Input 
	// 		value={this.state.search.name}
	// 		suffix={this.state.search.name ? <Icon 
	// 		type="close-circle" 
	// 		onClick={_ => this.changeSearch({name: ''})} /> : null}
	// 		onChange={e => this.changeSearch({name: e.target.value})}
	// 		addonBefore="名称" placeholder="输入字典数据名称" />
			
	// 		<Button 
	// 		onClick={this.search}
	// 		type="primary" icon="search">搜索</Button>
	// 	</div>
	// </div>

	renderBtn = _ => <div>
	<ButtonGroup>
		<Button 
		onClick={_ => this.addmodal('modalconf', '添加数据')}
		type="primary" icon="plus">添加</Button>
		<Button 
		onClick={_ => this.editmodal('modalconf', '编辑数据')}
		type="primary" icon="edit">修改</Button>
		<Button 
		onClick={this.changemodel}
		type="primary" icon="close">删除</Button>
	</ButtonGroup>
	</div>

	rendermodal = _ => [<DictDataForm 
		onCancel={_ => this.cancelform('modalconf')}
		done={_ => this.done()}
		dictid={this.state.dictid}
		istree={this.state.istree}
		getTree={_ => this.search()}
		treeData={this.state.treeMenu}
		config={this.state.modalconf} />,
		<Modal title="信息"
		  visible={this.state.visible}
		  onOk={this.delete}
		  mask={false}
		  width={400}
		  onCancel={this.changemodel}
		  okText="确认"
		  cancelText="取消"
		>
			<p>是否删除？</p>
		</Modal>]

}

export default DictData


